.word {
  display: inline-block;
  position: relative;
  transition: transform 700ms ease-in-out;
}
.correct {
  color: #6aa84f;
  font-weight: 400; /*500;*/
}
.incorrect {
  color: #797270;
  font-weight: 400;
  text-decoration-line: underline;
  text-decoration-style: wavy;
  text-decoration-color: #ffa8a9bb;
  text-decoration-thickness: 2px;
}

@keyframes incorrectHighlight {
  100.0% {
    color: red; /* #ffa8a9; */
    font-weight: 800;
  }
}

.flashOnIncorrect {
  animation: incorrectHighlight 150ms linear 0s;
}

.wordSelected {
  text-decoration: underline solid #453f3c;
  -webkit-text-decoration: underline solid #453f3c;
  color: #453f3c;
}
.wordSwap {
  transform: translate(var(--transLeft), var(--transTop));
}
.nowrap {
  white-space: nowrap;
}
.punctuation {
}
a {
  color: #92b4f4;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
